<template>
  <div class="main">
    <span class="headerTitle">生源地助学贷款学生在线系统</span>
    <el-card class="box-card">
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="帐号密码登录" name="first">
          <div>
            <el-input size="large" placeholder="请输入帐号" v-model="username" class="input">
              <template slot="prepend">
                <div class="inputHeader">
                  <i class="el-icon-user"></i>
                  <span>帐号</span>
                </div>
              </template>
            </el-input>
            <el-input
              size="large"
              placeholder="请输入密码"
              v-model="password"
              show-password
              class="input"
            >
              <template slot="prepend">
                <div class="inputHeader">
                  <i class="el-icon-lock"></i>
                  <span>密码</span>
                </div>
              </template>
            </el-input>
            <el-input
              size="large"
              placeholder="不区分大小写"
              v-model="verifyCode"
              class="input"
              @keyup.enter.native="Login"
            >
              <template slot="append">
                <div class="ValidCode disabled-select" @click="refreshCode">
                  <span
                    v-for="(item, index) in codeList"
                    :key="index"
                    :style="getStyle(item)"
                  >{{item.code}}</span>
                </div>
              </template>
            </el-input>

            <div class="btnGround">
              <el-button type="success" @click="Login" :disabled="loginBtnDisplay">登录</el-button>
              <el-button type="primary" @click="dialogFormVisible = true">注册</el-button>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="手机号登录" name="second">
          <div>
            <el-input size="large" placeholder="请输入手机号" v-model="photo" class="input">
              <template slot="prepend">
                <div class="inputHeader">
                  <i class="el-icon-phone-outline"></i>
                  <span>手机号</span>
                </div>
              </template>
            </el-input>
            <el-input size="large" placeholder="请输入短信验证码" v-model="verifyCode3" class="input">
              <template slot="prepend">
                <div class="inputHeader">
                  <i class="el-icon-lock"></i>
                  <span>验证码</span>
                </div>
              </template>
              <template slot="suffix">
                <span class="btnCode" @click="getCode(photo)">获取验证码</span>
              </template>
            </el-input>
            <el-input
              size="large"
              placeholder="不区分大小写"
              v-model="verifyCode2"
              class="input"
              @keyup.enter.native="Login2"
            >
              <template slot="append">
                <div class="ValidCode disabled-select" @click="refreshCode">
                  <span
                    v-for="(item, index) in codeList"
                    :key="index"
                    :style="getStyle(item)"
                  >{{item.code}}</span>
                </div>
              </template>
            </el-input>
            <div class="btnGround">
              <el-button type="success" @click="Login2" :disabled="loginBtnDisplay">登录</el-button>
              <el-button type="primary" @click="dialogFormVisible = true">注册</el-button>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
      <div class="bottom">
        <el-link type="primary" class="bottomBtn" @click="dialogPasswordVisible = true">忘记密码</el-link>
        <el-link type="primary" class="bottomBtn" @click="dialogInfoVisibel = true">常见问题</el-link>
      </div>
    </el-card>
    <el-dialog title="新用户注册" :visible.sync="dialogFormVisible" :close-on-click-modal="false" destroy-on-close>
      <el-form :model="form" label-position="top" size="small" ref="registerForm" :rules="rules">
        <el-form-item label="姓名" prop="userName">
          <el-input v-model="form.userName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="身份证号" prop="userIdentity">
          <el-input v-model="form.userIdentity" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="保密问题" prop="userSecurityQuestion">
          <el-select v-model="form.userSecurityQuestion" placeholder="请选择">
            <el-option label="我就读的小学" value="我就读的小学"></el-option>
            <el-option label="我就读的高中" value="我就读的高中"></el-option>
            <el-option label="我最喜欢的歌曲" value="我最喜欢的歌曲"></el-option>
            <el-option label="我最爱的人" value="我最爱的人"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="密保答案" prop="userSecurityAnswer">
          <el-input v-model="form.userSecurityAnswer" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="userPassword">
          <el-input v-model="form.userPassword" show-password autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="confirmPassword">
          <el-input v-model="form.confirmPassword" show-password autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="userPhone">
          <div class="formPhoto">
            <el-input v-model="form.userPhone" autocomplete="off"></el-input>
            <el-button type="primary" size="small" @click="getCode(form.userPhone)">发送验证码</el-button>
          </div>
        </el-form-item>
        <el-form-item label="短信验证码" prop="code">
          <el-input v-model="form.code" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button type="primary" @click="Register" size="medium">注 册</el-button>
      </div>
    </el-dialog>
    <!-- 常见问题 -->
    <el-dialog
      title="常见问题"
      :visible.sync="dialogInfoVisibel"
      :close-on-click-modal="false"
      class="dialogInfo"
    >
      <div style="padding:28px;background:#f5f5f5">
        <div class="questionTitle">一、还款、还利息、提前还款、扣款日、扣款审批中</div>
        <div
          class="questionContent"
        >建议您登陆学生在线服务系统（http://www.csls.cdb.com.cn）或阅读借款合同查询您的还款计划，并按期足额还款。 要还未到期的本金必须先登录学生在线服务系统申请提前还款。每月（除了11月）的15日（含）前的申请在当月20日扣款（扣款处理可能会持续到21号），15日之后的申请下月20日扣款。 如果您的助学贷款账户为支付宝账户(****.cdb@sina.cn)，那么您可以使用主动还款功能，或存钱到此支付宝余额里等系统自动扣款。 另外您或家人朋友也可以去全国任意一个支持POS机还款的资助中心，用银联卡还款。 扣款后到月底，在学生在线系统显示“扣款审批中”是正常的，您可以在下个月初再次登录学生在线服务系统查询还款完成情况。 如果申请了提前还款，却没还钱，不影响信用，下月可继续申请。毕业前还清贷款无利息（注意：要在15号之前做提前还款申请），无需做毕业确认。</div>
        <div class="questionTitle">二、逾期、不良记录、非恶意欠款证明、结清证明</div>
        <div
          class="questionContent"
        >根据征信业管理相关规定，您的逾期记录将在逾期事件终止（贷款结清）5年后自动清除。您可以还清贷款后联系资助中心老师，看能否开非恶意欠款证明。无特殊情况是不能开具。 贷款结清后，次月初您可以联系县中心老师或分行经办人开具贷款结清证明。</div>
        <div class="questionTitle">三、初次贷款申请，续贷</div>
        <div
          class="questionContent"
        >申请时间、截止时间：请咨询当地资助中心。各地时间不一样。 申请条件：考取了我行可以开展助学贷款的全日制普通本科高校、高等职业学校和高等专科学校（含民办高校和独立学院）、科研院所、党校、行政学院、会计学院（学校名单以教育部公布的为准），且满足家庭经济困难条件。 贷款金额：本专科生每生每年不超过8000元，研究生每生每年不超过12000元，贷款额度可按学费和住宿费标准总和确定。 携带材料：首次贷款，您可以登陆学生在线服务系统（http://www.csls.cdb.com.cn）在线填写申请信息并下载打印申请表。未通过预申请学生还需要提供借款学生和共同借款人本人签字的家庭经济困难学生认定申请表。然后需携带您的身份证、学生证、户口簿、录取通知书及申请表、认定表等材料和共同借款人（生源地）的身份证及户口簿，和共同借款人一起前往县级资助中心办理贷款申请。 续贷时，您或共同借款人任意一方携带身份证和您本人签字的申请表前往县资助中心办理续贷手续即可。续贷无需进行资格审查，申请表无需盖章。</div>
        <div class="questionTitle">四、贷款发放</div>
        <div
          class="questionContent"
        >如您的合同审批通过，贷款将在11月中旬发放。具体时间不确定，请耐心等待。贷款一部分发放到高校，剩下的发放到你的还款账号支付宝(****.cdb@sina.cn)上。 如果贷款发放了，自己又交了学费或学校扣多了钱，请联系高校退款。</div>
        <div class="questionTitle">五、支付宝密码、学生在线系统密码、支付宝账号不存在</div>
        <div
          class="questionContent"
        >你可以登录学生在线服务系统（ http://www.csls.cdb.com.cn）查询还款账号(****.cdb@sina.cn)和支付宝初始密码（也是新浪邮箱账号和密码），如果忘记支付宝登录密码，您可以通过新浪邮箱找回，或拨打支付宝24小时服务热线 95188。 支付宝账号不存在，可能是登录名改了，你可以问95188，查一下你身份证号有哪几个支付宝账号，哪个是由助学贷款改过来的，用改后的登录名登录就行。</div>
        <div class="questionTitle">六、考研（硕士/博士）、专升本、入伍、休学、留级、修改学制/入学年份</div>
        <div
          class="questionContent"
        >您应带相关证明，联系所在资助中心请老师帮忙尽快做好就学信息变更和还款计划变更。 我行没有明确办理截止日期，但建议您在贴息截止日前办理。当年10月15号之前没有办理，需要自己支付当年利息。 考研或专升本，还想续贷，请做完上述变更后，再申请续贷。</div>
        <div class="questionTitle">七、毕业确认</div>
        <div
          class="questionContent"
        >毕业前还清贷款可以不做毕业确认。毕业后升学可以不做毕业确认。毕业后升学续贷可以不做毕业确认。做了也没关系。 毕业确认操作：学生登陆学生在线服务系统（http://www.csls.cdb.com.cn），在“毕业确认申请”功能中提交毕业确认申请。高校老师对学生的毕业确认申请进行审核。 毕业后按期足额还款就行，无其它手续。</div>
        <div slot="footer" class="button">
          <el-button type="primary" @click="dialogInfoVisibel = false" size="medium">确 定</el-button>
        </div>
      </div>
    </el-dialog>
    <!-- 重置方式 -->
    <el-dialog
      :title="dialogIdentity?'请输入您的要重置密码的身份证号码':'请选择您重置密码的方式'"
      :visible.sync="dialogPasswordVisible"
      :close-on-click-modal="false"
      @closed="dialogIdentity=false"
    >
      <div class="dialogPassword" v-if="!dialogIdentity">
        <div class="passwordCard">
          <div class="passwordText">
            <div class="passwordTitle">通过短信验证码重置密码</div>
            <div class="passwordContent">通过您绑定手机号码找回密码</div>
          </div>

          <el-button type="primary" size="small" @click="dialogIdentity = true;dialogType = 1">进入</el-button>
          <div></div>
        </div>
        <div class="passwordCard">
          <div class="passwordText">
            <div class="passwordTitle">通过密码保护问题重置密码</div>
          </div>

          <el-button type="primary" size="small" @click="dialogIdentity = true;dialogType = 2">进入</el-button>
          <div></div>
        </div>
      </div>
      <div v-else style="padding:10px 0 30px 0">
        <el-form
          label-position="top"
          size="small"
          style="width:300px;margin-bottom:30px"
          ref="identityForm"
          :model="dialogFrom"
          :rules="rule"
        >
          <el-form-item label="身份证号" prop="identity">
            <el-input v-model="dialogFrom.identity" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="验证码" prop="code">
            <el-input size="large" placeholder="不区分大小写" v-model="dialogFrom.code">
              <template slot="append">
                <div class="ValidCode disabled-select" @click="refreshCode">
                  <span
                    v-for="(item, index) in codeList"
                    :key="index"
                    :style="getStyle(item)"
                  >{{item.code}}</span>
                </div>
              </template>
            </el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer" style="margin-top:10px">
          <el-button @click="dialogIdentity = false">返 回</el-button>
          <el-button type="primary" @click="checkPassword">下 一 步</el-button>
        </span>
      </div>
    </el-dialog>
    <el-dialog title="通过手机短信验证码重置密码" :visible.sync="dialog1" :close-on-click-modal="false" destroy-on-close>
      <div>已更换手机号，请通过其它方式找回密码</div>
      <div>短信验证码将发送至{{dialog1Form.userPhone}}手机号</div>
      <el-form
        :model="dialog1Form"
        label-width="145px"
        label-position="top"
        style="width:400px"
        ref="updateForm1"
        :rules="rules1"
      >
        <el-form-item label="验证码" prop="code">
          <div style="display:flex;align-items: center;">
            <el-input size="large" placeholder="不区分大小写" v-model="dialog1Form.code" class="input">
              <template slot="append">
                <div class="ValidCode disabled-select" @click="refreshCode">
                  <span
                    v-for="(item, index) in codeList"
                    :key="index"
                    :style="getStyle(item)"
                  >{{item.code}}</span>
                </div>
              </template>
            </el-input>
            <el-button
              type="primary"
              size="small"
              style="height:40px;margin-left:10px"
              @click="getCode(dialog1Form.userPhone)"
            >发送验证码</el-button>
          </div>
        </el-form-item>
        <el-form-item label="手机验证码" prop="verifyCode">
          <el-input size="small" v-model="dialog1Form.verifyCode"></el-input>
        </el-form-item>
        <el-form-item label="请输入新密码" prop="newPwd">
          <el-input size="small" v-model="dialog1Form.newPwd" show-password></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="confirmNewPwd">
          <el-input size="small" v-model="dialog1Form.confirmNewPwd" show-password></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogPasswordVisible = true;dialogIdentity = true;dialog1=false;">返 回</el-button>
        <el-button type="primary" @click="updatePassword">提 交</el-button>
      </span>
    </el-dialog>
    <el-dialog title="通过密码保护问题重置密码" :visible.sync="dialog2" :close-on-click-modal="false" destroy-on-close>
      <el-form
        :model="dialog2Form"
        label-position="top"
        size="small"
        ref="updateForm2"
        :rules="rules2"
      >
        <el-form-item label="密保问题">
          <el-input v-model="dialog2Form.userSecurityQuestion" autocomplete="off" disabled></el-input>
        </el-form-item>
        <el-form-item label="答案" prop="securityAnswer">
          <el-input v-model="dialog2Form.securityAnswer" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="请输入新密码" prop="newPwd">
          <el-input size="small" v-model="dialog2Form.newPwd" show-password></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="confirmNewPwd">
          <el-input size="small" v-model="dialog2Form.confirmNewPwd" show-password></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogPasswordVisible = true;dialogIdentity = true;dialog1=false;">返 回</el-button>
        <el-button type="primary" @click="updatePassword2">提 交</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      activeName: "first",
      username: "522628199801236616",
      password: "123456",
      photo: "",

      dialogFrom: {
        identity: "",
        code: ""
      },

      dialogType: 0,
      verifyCode: "",
      verifyCode2: "",
      verifyCode3: "",

      codeList: [],
      confirmcode: "",
      dialogFormVisible: false,
      dialogInfoVisibel: false,
      dialogPasswordVisible: false,
      dialog1: false,
      dialog2: false,
      dialog1Form: {
        userPhone: "",
        newPwd: "",
        code: "",
        verifyCode: "",
        confirmNewPwd: ""
      },
      dialog2Form: {
        userSecurityQuestion: "",
        securityAnswer: "",
        newPwd: "",
        confirmNewPwd: ""
      },
      formPassword: "",
      loginBtnDisplay: false,
      dialogIdentity: false,
      form: {
        userName: "",
        userIdentity: "",
        userSecurityQuestion: "",
        userSecurityAnswer: "",
        userPassword: "",
        confirmPassword: "",
        userPhone: ""
      },
      rules: {
        userName: [
          { required: true, message: "姓名不能为空", trigger: "blur" },
          {
            min: 2,
            message: "中文最少输入两位，英文最少输入三位",
            trigger: "blur"
          },
          {
            max: 15,
            message: '只能输入中文和大写英文以及全角下的" · "且最多15位',
            trigger: "blur"
          }
        ],
        userIdentity: [
          { required: true, message: "身份证号不能为空", trigger: "blur" },
          { min: 15, max: 18, message: "身份证不合法", trigger: "blur" },
          {
            required: true,
            pattern: /(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)|(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)/,
            message: "身份证不合法",
            trigger: "blur"
          }
        ],
        userSecurityQuestion: [
          { required: true, message: "密保问题不能为空", trigger: "change" }
        ],
        userSecurityAnswer: [
          { required: true, message: "密保答案不能为空", trigger: "blur" }
        ],
        userPassword: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "密码为6到20位数字与字母组合",
            trigger: "blur"
          }
        ],
        confirmPassword: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "密码为6到20位数字与字母组合",
            trigger: "blur"
          },
          {
            validator: (rules, value, callback) => {
              if (this.form.userPassword !== value) {
                return callback(new Error("密码不相同"));
              } else {
                return callback();
              }
            },
            trigger: "blur"
          }
        ],
        userPhone: [
          { required: true, message: "手机号不能为空", trigger: "blur" }
        ],
        code: [{ required: true, message: "验证码不能为空", trigger: "blur" }]
      },
      rule: {
        identity: [
          { required: true, message: "身份证号不能为空", trigger: "blur" },
          { min: 15, max: 18, message: "身份证不合法", trigger: "blur" },
          {
            required: true,
            pattern: /(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)|(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)/,
            message: "身份证不合法",
            trigger: "blur"
          }
        ],
        code: [
          { required: true, message: "验证码不能为空", trigger: "blur" },
          {
            validator: (rules, value, callback) => {
              if (this.confirmcode.toUpperCase() !== value.toUpperCase()) {
                return callback(new Error("验证码不相同"));
              }
              return callback();
            },
            trigger: "blur"
          }
        ]
      },
      rules1: {
        newPwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "密码为6到20位数字与字母组合",
            trigger: "blur"
          }
        ],
        confirmNewPwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "密码为6到20位数字与字母组合",
            trigger: "blur"
          },
          {
            validator: (rules, value, callback) => {
              if (this.dialog1Form.newPwd !== value) {
                return callback(new Error("密码不相同"));
              }
              return callback();
            },
            trigger: "blur"
          }
        ],
        code: [
          { required: true, message: "验证码不能为空", trigger: "blur" },
          {
            validator: (rules, value, callback) => {
              if (this.confirmcode.toUpperCase() !== value.toUpperCase()) {
                return callback(new Error("验证码不相同"));
              }
              return callback();
            },
            trigger: "blur"
          }
        ],
        verifyCode: [
          { required: true, message: "手机验证码不能为空", trigger: "blur" }
        ]
      },
      rules2: {
        securityAnswer: [
          { required: true, message: "密保答案不能为空", trigger: "blur" }
        ],
        newPwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "密码为6到20位数字与字母组合",
            trigger: "blur"
          }
        ],
        confirmNewPwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "密码为6到20位数字与字母组合",
            trigger: "blur"
          },
          {
            validator: (rules, value, callback) => {
              if (this.dialog2Form.newPwd !== value) {
                return callback(new Error("密码不相同"));
              }
              return callback();
            },
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {
    this.createdCode();
  },
  methods: {
    refreshCode() {
      this.createdCode();
    },
    createdCode() {
      let len = 4,
        codeList = [],
        chars = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz0123456789",
        charsLen = chars.length;
      // 生成
      for (let i = 0; i < len; i++) {
        let rgb = [
          Math.round(Math.random() * 220),
          Math.round(Math.random() * 240),
          Math.round(Math.random() * 200)
        ];
        codeList.push({
          code: chars.charAt(Math.floor(Math.random() * charsLen)), // 随机码
          color: `rgb(${rgb})`, // 随机颜色
          fontSize: 16 + Number(Math.floor(Math.random() * 15)) + "px", // 随机字号
          padding: `${[Math.floor(Math.random() * 10)]}px`, // 随机内边距
          transform: `rotate(${Math.floor(Math.random() * 90) -
            Math.floor(Math.random() * 90)}deg)` // 随机旋转角度
        });
      }
      // 指向
      this.codeList = codeList;
      this.confirmcode = codeList.map(item => item.code).join("");
    },
    // 动态绑定样式
    getStyle(data) {
      return `color: ${data.color}; font-size: ${data.fontSize}; padding: ${data.padding}; transform: ${data.transform}`;
    },
    //帐号密码登录
    Login() {
      this.loginBtnDisplay = true;
      if (
        !this.username ||
        !this.password ||
        this.verifyCode.toUpperCase() != this.confirmcode.toUpperCase()
      ) {
        this.$message({
          type: "error",
          message: "请填写正确的信息",
          center: true,
          onClose: () => {
            this.loginBtnDisplay = false;
          }
        });
        return;
      }
      this.$axios
        .post(
          "/userLogin/byAccount",
          {
            userLoginAccount: this.username,
            userLoginPassword: this.password
          },
          {
            ignore: true
          }
        )
        .then(({ status, data }) => {
          if (status == 200) {
            this.$message({
              message: "登录成功",
              type: "success"
            });
            sessionStorage.setItem("token", data.token);
            sessionStorage.setItem("userLoginAccount", data.userLoginAccount);
            this.$router.push({ name: "home" });
          }
          if (status == 401) {
            this.$message({
              message: "该用户不存在"
            });
          }
          if (status == 1000) {
            this.$message({
              message: "用户帐号密码错误",
              type: "error"
            });
          }
          this.refreshCode();
          this.verifyCode = '';
          this.loginBtnDisplay = false;
        })
        .catch(error => {
          this.loginBtnDisplay = false;
          console.log(error);
        });
    },
    //手机号登录
    Login2() {
      this.loginBtnDisplay = true;
      if (
        !this.photo ||
        !this.verifyCode3 ||
        this.verifyCode2.toUpperCase() != this.confirmcode.toUpperCase()
      ) {
        this.$message({
          type: "error",
          message: "请填写正确的信息",
          center: true,
          onClose: () => {
            this.loginBtnDisplay = false;
          }
        });
        return;
      }
      this.$axios
        .post(
          "/userLogin/loginCheck",
          {
            userPhone: this.photo,
            verifyCode: this.verifyCode3
          },
          {
            ignore: true
          }
        )
        .then(({ status, data }) => {
          if (status == 200) {
            this.$message({
              message: "登录成功",
              type: "success"
            });
            sessionStorage.setItem("token", data.token);
            sessionStorage.setItem("userLoginAccount", data.userLoginAccount);
            this.$router.push({ name: "home" });
          }
          if (status == 401) {
            this.$message({
              message: "登录失败,验证码错误",
              type: "error"
            });
          }
          if (status == 402) {
            this.$message({
              message: "登录失败，验证码过期",
              type: "error"
            });
          }
          this.refreshCode();
          this.verifyCode = '';
          this.loginBtnDisplay = false;
        })
        .catch(error => {
          this.loginBtnDisplay = false;
          console.log(error);
        });
    },
    //注册弹窗注册
    Register() {
      const { form } = this;
      this.$refs.registerForm.validate(valid => {
        if (valid) {
          this.$axios
            .post(
              "/user/register",
              {
                ...form
              },
              {
                ignore: true
              }
            )
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "注册成功",
                  type: "success"
                });
                 this.dialogFormVisible = false;
              } else if(status == 401){
                this.$message({
                  message: "注册失败",
                  type: "error"
                });
                  this.refreshCode();
                  this.verifyCode = '';
              }else if(status == 500){
                 this.$message({
                  message: "注册失败，该账号已注册",
                  type: "warning"
                });
                 this.dialogFormVisible = false;
              }
              
             
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          return false;
        }
      });
    },
    //获取验证码
    getCode(userPhone) {
      if (!userPhone) {
        this.$message({
          message: "手机号不能为空",
          type: "warning"
        });
        return;
      }
      this.$axios
        .post(
          "/getCode",
          {
            userPhone
          },
          {
            ignore: true
          }
        )
        .then(({ status }) => {
          if (status == 200) {
            this.$message({
              message: "短信已发送",
              type: "success"
            });
          } else {
            this.$message({
              message: "短信发送失败",
              type: "error"
            });
          }
          this.refreshCode();
        })
        .catch(error => {
          console.log(error);
        });
    },
    //验证身份证
    checkPassword() {
      this.$refs.identityForm.validate(valid => {
        if (valid) {
          this.$axios
            .post(
              "/user/verifyUser",
              {
                userIdentity: this.dialogFrom.identity
              },
              {
                ignore: true
              }
            )
            .then(({ status }) => {
              if (status == 200) {
                const dialog = "dialog" + this.dialogType;
                this[dialog] = true;
                this.dialogPasswordVisible = false;
                if (this.dialogType == 1) {
                  this.$axios
                    .post(
                      "/user/selectPhoneByIdentity",
                      {
                        userIdentity: this.dialogFrom.identity
                      },
                      {
                        ignore: true
                      }
                    )
                    .then(({ status, data }) => {
                      if (status == 200) {
                        this.dialog1Form.userPhone = data.userPhone;
                      } else {
                        this.$message({
                          message: "查找失败",
                          type: "error"
                        });
                      }
                    });
                } else {
                  this.$axios
                    .post(
                      "/user/selectSecurityByIdentity",
                      {
                        userIdentity: this.dialogFrom.identity
                      },
                      {
                        ignore: true
                      }
                    )
                    .then(({ status, data }) => {
                      if (status == 200) {
                        this.dialog2Form.userSecurityQuestion =
                          data.userSecurityQuestion;
                      } else {
                        this.$message({
                          message: "查找失败",
                          type: "error"
                        });
                      }
                    });
                }
              } else {
                this.$message({
                  message: "身份证不合法",
                  type: "error"
                });
              }
                this.refreshCode();
                this.verifyCode = '';
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          return false;
        }
      });
    },
    //修改密码
    updatePassword() {
      this.$refs.updateForm1.validate(valid => {
        if (valid) {
          this.$axios
            .post(
              "/userLogin/resetPwdByUserPhone",
              {
                userIdentity: this.dialogFrom.identity,
                verifyCode: this.dialog1Form.verifyCode,
                newPwd: this.dialog1Form.newPwd
              },
              {
                ignore: true
              }
            )
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "密码重置成功",
                  type: "success"
                });
                this.dialog1 = false;
              } else if (status == 401) {
                this.$message({
                  message: "密码重置失败，验证码错误",
                  type: "error"
                });
                   this.refreshCode();
                  this.verifyCode = '';
              } else if (status == 402) {
                this.$message({
                  message: "密码重置失败，验证码过期",
                  type: "error"
                });
                   this.refreshCode();
                  this.verifyCode = '';
              } else if (status == 500) {
                this.$message({
                  message: "密码重置失败，系统异常",
                  type: "error"
                });
                   this.refreshCode();
                  this.verifyCode = '';
              }
            });
        } else {
          return false;
        }
      });
    },
    //修改密码2
    updatePassword2() {
      this.$refs.updateForm2.validate(valid => {
        if (valid) {
          this.$axios
            .post(
              "/userLogin/resetPwdByUserSecurity",
              {
                userIdentity: this.dialogFrom.identity,
                securityAnswer: this.dialog2Form.securityAnswer,
                newPwd: this.dialog2Form.newPwd
              },
              {
                ignore: true
              }
            )
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "重置成功",
                  type: "success"
                });
                this.dialog2 = false;
              } else if (status == 401) {
                this.$message({
                  message: "重置失败,密保答案错误",
                  type: "error"
                });
              } else if (status == 500) {
                this.$message({
                  message: "密码重置失败，系统异常",
                  type: "error"
                });
              }
            });
        } else {
          return false;
        }
      });
    }
  }
};
</script>
