行经办人开具贷款结清证明。")]),_c('div',{staticClass:"questionTitle"},[_vm._v("三、初次贷款申请，续贷")]),_c('div',{staticClass:"questionContent"},[_vm._v("申请时间、截止时间：请咨询当地资助中心。各地时间不一样。 申请条件：考取了我行可以开展助学贷款的全日制普通本科高校、高等职业学校和高等专科学校（含民办高校和独立学院）、科研院所、党校、行政学院、会计学院（学校名单以教育部公布的为准），且满足家庭经济困难条件。 贷款金额：本专科生每生每年不超过8000元，研究生每生每年不超过12000元，贷款额度可按学费和住宿费标准总和确定。 携带材料：首次贷款，您可以登陆学生在线服务系统（http://www.csls.cdb.com.cn）在线填写申请信息并下载打印申请表。未通过预申请学生还需要提供借款学生和共同借款人本人签字的家庭经济困难学生认定申请表。然后需携带您的身份证、学生证、户口簿、录取通知书及申请表、认定表等材料和共同借款人（生源地）的身份证及户口簿，和共同借款人一起前往县级资助中心办理贷款申请。 续贷时，您或共同借款人任意一方携带身份证和您本人签字的申请表前往县资助中心办理续贷手续即可。续贷无需进行资格审查，申请表无需盖章。")]),_c('div',{staticClass:"questionTitle"},[_vm._v("四、贷款发放")]),_c('div',{staticClass:"questionContent"},[_vm._v("如您的合同审批通过，贷款将在11月中旬发放。具体时间不确定，请耐心等待。贷款一部分发放到高校，剩下的发放到你的还款账号支付宝(****.cdb@sina.cn)上。 如果贷款发放了，自己又交了学费或学校扣多了钱，请联系高校退款。")]),_c('div',{staticClass:"questionTitle"},[_vm._v("五、支付宝密码、学生在线系统密码、支付宝账号不存在")]),_c('div',{staticClass:"questionContent"},[_vm._v("你可以登录学生在线服务系统（ http://www.csls.cdb.com.cn）查询还款账号(****.cdb@sina.cn)和支付宝初始密码（也是新浪邮箱账号和密码），如果忘记支付宝登录密码，您可以通过新浪邮箱找回，或拨打支付宝24小时服务热线 95188。 支付宝账号不存在，可能是登录名改了，你可以问95188，查一下你身份证号有哪几个支付宝账号，哪个是由助学贷款改过来的，用改后的登录名登录就行。")]),_c('div',{staticClass:"questionTitle"},[_vm._v("六、考研（硕士/博士）、专升本、入伍、休学、留级、修改学制/入学年份")]),_c('div',{staticClass:"questionContent"},[_vm._v("您应带相关证明，联系所在资助中心请老师帮忙尽快做好就学信息变更和还款计划变更。 我行没有明确办理截止日期，但建议您在贴息截止日前办理。当年10月15号之前没有办理，需要自己支付当年利息。 考研或专升本，还想续贷，请做完上述变更后，再申请续贷。")]),_c('div',{staticClass:"questionTitle"},[_vm._v("七、毕业确认")]),_c('div',{staticClass:"questionContent"},[_vm._v("毕业前还清贷款可以不做毕业确认。毕业后升学可以不做毕业确认。毕业后升学续贷可以不做毕业确认。做了也没关系。 毕业确认操作：学生登陆学生在线服务系统（http://www.csls.cdb.com.cn），在“毕业确认申请”功能中提交毕业确认申请。高校老师对学生的毕业确认申请进行审核。 毕业后按期足额还款就行，无其它手续。")]),_c('div',{staticClass:"button",attrs:{"slot":"footer"},slot:"footer"},[_c('el-button',{attrs:{"type":"primary","size":"medium"},on:{"click":function($event){_vm.dialogInfoVisibel = false}}},[_vm._v("确 定")])],1)])]),_c('el-dialog',{attrs:{"title":_vm.dialogIdentity?'请输入您的要重置密码的身份证号码':'请选择您重置密码的方式',"visible":_vm.dial